import React from "react";
import Card from "../components/card";
import cardInfo from "../local-json/local-orga-card";
import "../dist/css/main.css";

var cardComponent = cardInfo.map(element => <Card data={element}></Card>);

const Organizers = () => {
    return (
        <div className="orga__container">
            <h1>Orga</h1>
            <p>Wer sind wir? Ein schniekes Team</p>
            <div className="orga__card-container">
            {cardComponent}
            </div>
        </div>
    );
};

export default Organizers;
