import React from "react";
import "../dist/css/main.css";

var Card = obj => {
    console.log(obj);
    return (
        <div class="card-container">
            <div class="left-box">
                <div class="right-box__headline">{obj.data.name}</div>
                <div class="right-box__content">{obj.data.info}</div>
            </div>
            <div class="middle-box">
                <div className="middle-box__category">{obj.data.job}</div>
                <div className="middle-box__image">
                    <img
                        src={require("../dist/images/" +
                            obj.data.image +
                            ".png")}
                        className="middle-box__image--size"
                        alt=""
                    />
                </div>
                <div className="middle-box__category">{obj.data.date}</div>
            </div>
            <div class="right-box">
                <div class="right-box__headline">{obj.data.name}</div>
                <div class="right-box__content">{obj.data.info}</div>
            </div>
        </div>
    );
};

export default Card;
